.modalBackdrop {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(2px);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
}

.modalBackdrop:hover {
  /* cursor: pointer; */
}

.modalBackdropWithHover {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(2px);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
}

.modalBackdropWithHover:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.3);
}

.modalHelper {
  display: flex;
}

.modalMain {
  text-align: center;
  padding: 1rem;
  margin: 1rem;
  background-color: salmon;
  border: 1px white solid;
  border-radius: 1rem;
  /* font-size: 1vw; */
}

.removeElementNav {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right: 1rem;
}

@media (orientation: portrait) {
  .modalMain {
    /* font-size: 2vh; */
  }
}
